.grid {
  display: flex;
  flex-flow: row wrap;
  position: relative;
  margin-left: -#{$grid-gutter};
  margin-right: -#{$grid-gutter};

  [class^="grid-"],
  [class*=" grid-"] {
    padding: #{$grid-gutter};
  }

  &.alt {
    [class^="grid-"],
    [class*=" grid-"] {

      &:first-of-type {
        @include at-least($tablet){
            padding-right: 40px;
        }
      }
    }
  }
}

@for $i from 1 through $grid-columns {
  .grid-xs-#{$i} {
    width: calc(100% / (#{$grid-columns} / #{$i}));
  }

  .offset-xs-#{$i} {
    margin-left: calc(100% / (#{$grid-columns} / #{$i}));
  }
}

@include at-least($mobile) {
  @for $i from 1 through $grid-columns {
    .grid-sm-#{$i} {
      width: calc(100% / (#{$grid-columns} / #{$i}));
    }

    .offset-sm-#{$i} {
      margin-left: calc(100% / (#{$grid-columns} / #{$i}));
    }
  }
}

